<template>
	<div class='shouye'>
		<div class="banner">
			<swiper :options="swiperOption" ref="mySwiper">
				<!-- slides -->
				<!--<swiper-slide v-for='(value,key) in urls'>
					<img :src="value"/>
				</swiper-slide>-->
				<!--<swiper-slide>
					<img src="../assets/home/banner1.png" />
				</swiper-slide>-->
				<swiper-slide>
					<img src="../assets/home/new_banner1.png" />
					<div class="slide_wrap banner_slide1">
						<div></div>
						<p></p>
					</div>
				</swiper-slide>
				<swiper-slide>
					<img src="../assets/home/new_banner3.png" />
					<div class="slide_wrap banner_slide3">
						<div>JOIN<span>US</span></div>
						<p></p>
					</div>
				</swiper-slide>
				<swiper-slide>
					<img src="../assets/home/new_banner2.png" />
					<div class="slide_wrap banner_slide2">
						<div></div>
						<p>
							<span class="banners_lefts"></span>
							<span></span>
						</p>
					</div>
				</swiper-slide>

				<!-- Optional controls -->
				<div class="swiper-pagination" slot="pagination"></div>
			</swiper>
		</div>
		<div class="introduction">
			<div class="container">
				<div class="t1">
					<div class="h4"></div>
					<p>201582016</p>
					<p></p>
					<p></p>
				</div>
				<div class="t2">
					<div class="h4"></div>
					<p> 2 ,
					</p>
					<p>AndroidIOS APP Store</p>
				</div>
			</div>
		</div>
		<div class="lunbos_slide">
			<swiper :options="swiperPer" ref="mySwiper">
				<!-- slides -->
				<!--<swiper-slide v-for='(value,key) in urls'>
					<img :src="value"/>
				</swiper-slide>-->
				<swiper-slide>
					<img src="../assets/home/slide1.png" class="home_inner" />
					<div class="mengban"></div>
					<div class="text-wrap">
						<h6></h6>
						<p>APP</p>
						<p></p>
					</div>
				</swiper-slide>
				<swiper-slide>
					<img src="../assets/home/slide2.png" class="home_inner" />
					<div class="mengban"></div>
					<div class="text-wrap">
						<h6></h6>
						<p></p>
						<p></p>
					</div>
				</swiper-slide>
				<swiper-slide>
					<img src="../assets/home/slide3.png" class="home_inner" />
					<div class="mengban"></div>
					<div class="text-wrap">
						<h6></h6>
						<p></p>
						<p></p>
					</div>
				</swiper-slide>
				<!-- Optional controls -->
				<div class="swiper-pagination" slot="pagination"></div>
			</swiper>
		</div>
	</div>
</template>

<script>
	import { swiper } from 'vue-awesome-swiper'
	export default {
		data() {
			return {
				//				urls: ['../assets/home/banner1.png', '/static/img/banner2.png', '/static/img/banner3.png'],
				swiperOption: {
					pagination: '.swiper-pagination',
					//					paginationClickable: true,
					//					debugger: true,
					loop: true,
					//					effect: 'cube',
					autoplay: 3000,
					autoplayDisableOnInteraction: false,
					lazyLoading: true,
					lazyLoadingInPrevNext: true,
				},
				//
				swiperPer: {
					pagination: '.swiper-pagination',
					slidesPerView: 3,
					paginationClickable: true,
					grabCursor: true,
					watchSlidesProgress: true,
					watchSlidesVisibility: true,
					pagination: null
				},
			}
		},
		components: {
			swiper
		},
		mounted: function() {

		}
	}
</script>

<style scoped="scoped" lang="scss">
	.shouye {
		& .banner {
			height: 734px;
			position: relative;
			background-color: #222223;
			h4 {
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
			}
			& .swiper-container {
				width: 100%;
				height: 100%;
				margin-left: auto;
				margin-right: auto;
				position: relative;
				overflow: hidden;
				z-index: 1;
			}
		}
		.introduction {
			background: url(../assets/home/home_list3Bg.png) no-repeat center;
			background-size: 100% 100%;
			.container {
				text-align: left;
				padding-top: 47px;
				.t2 {
					padding-bottom: 112px;
				}
				.h4 {
					color: #373738;
					font-size: 28px;
					line-height: 143px;
					height: 143px;
					text-align: center;
				}
				p {
					color: #7d7d7d;
					font-size: 16px;
					line-height: 23px;
					text-indent: 2em;
					font-weight: 300;
				}
			}
		}
		.lunbos_slide {
			/*height:669px;*/
			& .swiper-container {
				/*height: 100%;*/
				.swiper-slide {
					box-sizing: border-box;
					/*border-left: 1px solid #373738;*/
					&:before{
						content: '';
						position: absolute;
						left: 0;
						width: 1px;
						height: 100%;
						background-color: #373738;
					}
					img {
						width: 100%;
						height: auto;
					}
					.text-wrap {
						position: absolute;
						right: 4.4%;
						text-align: left;
						top: 65.5%;
						display: none;
						&:before {
							position: absolute;
							width: 134px;
							height: 37px;
							background-color: #787877;
							content: '';
							left: -24px;
							top: 13px;
						}
						h6 {
							width: 134px;
							height: 37px;
							color: #f2f2f2;
							line-height: 37px;
							font-size: 24px;
							background-color: #515152;
							text-align: center;
							font-weight: 400;
							position: relative;
							margin-bottom: 43px;
						}
						p {
							color: #f9f9f9;
							font-size: 16px;
						}
					}
					&:nth-child(1) {
						border-left: none;
					}
					.mengban {
						position: absolute;
						width: 0;
						height: 0;
						left: 50%;
						top: 50%;
						border-radius: 50%;
						transform: translate(-50%, -50%);
						background-color: rgba(0, 0, 0, 0.51);
						left: 0;
						top: 0;
						opacity: 0;
					}
					&:hover .mengban {
						left: 50%;
						top: 50%;
						transform: translate(-50%, -50%);
						border-radius: 0;
						width: 100%;
						height: 100%;
						opacity: 1;
					}
					&:hover .text-wrap {
						display: block;
					}
				}
			}
		}
	}
	
	.swiper-slide {
		text-align: center;
		font-size: 18px;
		background: #fff;
		/* Center slide text vertically */
		display: -webkit-box;
		display: -ms-flexbox;
		display: -webkit-flex;
		display: flex;
		-webkit-box-pack: center;
		-ms-flex-pack: center;
		-webkit-justify-content: center;
		justify-content: center;
		-webkit-box-align: center;
		-ms-flex-align: center;
		-webkit-align-items: center;
		align-items: center;
		height: 100%;
		position: relative;
		overflow: hidden;
		&:after {
			clear: both;
			content: '';
			display: block;
		}
		img {
			/*flex: left;*/
			/*height: 100%;
			width: auto;*/
			height: auto;
			width: 100%;
		}
		.slide_wrap {
			position: absolute;
			color: #fff;
			div {
				font-size: 68px;
			}
			p {
				font-size: 32px;
			}
		}
		.banner_slide1 {
			position: absolute;
			/*left:41%;*/
			/*margin-left: 3%;*/
			left: 53%;
			top: 50%;
			transform: translate(-50%, -50%);
			white-space: nowrap;
			p {
				padding-left: 600px;
			}
		}
		.banner_slide2 {
			color: #3c3d3f;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			p {
				letter-spacing: 30px;
				white-space: nowrap;
				span{
					white-space: nowrap;
				}
				.banners_lefts {
					padding-right: 86px;
				}
			}
		}
		.banner_slide3 {
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			div {
				font-size: 72px;
				text-align: left;
				span {
					padding-left: 0.5em;
				}
			}
			p {
				font-size: 68px;
			}
		}
	}
</style>


